.CategoriesBar{
    position: fixed;
    display: flex;
    width: 85.8%;
    padding: 0em 1em;
    
    z-index: 998;
    overflow: hidden;
    list-style-type: none;
    -ms-overflow-style: none;
    scrollbar-width: none;

    .active {
        color:#444444;
        background-color: rgb(218, 218, 218);
    }

    li{
        margin: 1vh;
        overflow: visible;
        padding: 0.5rem ;
        border-radius: 5px;
        white-space: nowrap;
        color: whitesmoke;
        background-color: #444444; 
        cursor: pointer;
        
        &:hover{
            transition: 300ms;
            background-color: #616161; 
        }
    }
}

.CategoriesBar::-webkit-scrollbar {
    display: none;
}
